<template>
    <div>
        <filter-component :fields="filterFields" @handleSearch="handleSearch"/>
        <div style="height: 10px"></div>
        <table-component
                ref="table"
                @handleOperate="handleOperate"
                :columns="columns"
                :request="tableRequest">
        </table-component>
        <a-modal
                width="90%"
                :title="modalTitle"
                v-model="modalVisible"
                :footer="null"
                :destroyOnClose="true">
            <form-component :id="formId"/>
        </a-modal>
    </div>
</template>

<script>
    import {getExamResultList} from '@/api/examResult'
    import tableComponent from '@/components/table'
    import FormComponent from './components/Form'
    import filterComponent from '@/components/filter/Index'

    export default {
        components: {
            tableComponent, FormComponent,filterComponent
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                formId: 0,
                filterFields:[
                    {label:'人员',field:'user_id'},
                    {label:'考试开始时间',field:'start_timestamp'},
                    {label:'-',field:'end_timestamp'},
                ],
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '答题人',
                        dataIndex: 'user.name',
                        key: 'user.name',
                    },
                    {
                        title: '试卷名称',
                        dataIndex: 'paper.name',
                        key: 'paper.name',
                    },
                    {
                        title: '分数',
                        dataIndex: 'score',
                        key: 'score',
                    },
                    {
                        title: '是否通过',
                        dataIndex: 'is_passed',
                        key: 'is_passed',
                    },
                    {
                        title: '开始答题时间',
                        dataIndex: 'created_at',
                        key: 'created_at',
                    },
                    {
                        title: '结束答题时间',
                        dataIndex: 'updated_at',
                        key: 'updated_at',
                    },
                    // {
                    //     title: '操作',
                    //     dataIndex: 'operation',
                    //     scopedSlots: {customRender: 'operation'},
                    //     buttons: {
                    //         check: {visible: true, disable: false,},
                    //         edit: {visible: true, disable: false,},
                    //     }
                    // }
                ],
                tableRequest: getExamResultList,
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleCreate() {
                this.modalTitle = '新增分类';
                this.modalVisible = true;
                this.formId = null;
            },
            handleEdit(id) {
                this.modalTitle = '编辑分类';
                this.modalVisible = true;
                this.formId = id;
            },
            handleOperate(type, row) {
                switch (type) {
                    case 'create':
                        this.handleCreate();
                        break;
                    case 'edit':
                        this.handleEdit(row.id);
                        break;
                    case 'delete':
                        deleteExamResult(row.id)
                            .then(res => {
                                this.$message.success('删除成功');
                                this.$refs.table.reFetch();
                            });
                        break;
                }
            },
        },
    }
</script>

